body {
  font-family: "Oswald", "Arial", sans-serif;
  min-width: 320px;
  width: 100%;
  padding: 0;
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  color: #ffffff;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  padding: 10px;
  background-color: #68b738;
  display: block;
  box-sizing: border-box;
  width: 280px;
  margin: 0;
  text-align: center;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: #8fc46e;
}
.page-header {
  width: 100%;
  background-color: #ffffff;
}
.page-header__wrapper {
  padding: 15px 20px 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 320px;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
}
.page-header__logo {
  width: 195px;
  margin-bottom: 15px;
}
.page-header__logo:hover,
.page-header__logo:focus {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    flex-direction: row;
    padding: 0 30px;
    width: 768px;
  }
}
@media (min-width: 1300px) {
  .page-header__wrapper {
    padding: 0 40px;
    width: 1300px;
  }
}
.main-nav {
  text-align: center;
  margin: 0 auto;
}
.main-nav__toggle {
  position: absolute;
  top: 28px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 22px;
  height: 22px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover,
.main-nav__toggle:focus {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  left: 0;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-nav__item {
  font-size: 20px;
  line-height: 20px;
  position: relative;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
  display: block;
  padding-top: 23px;
  padding-bottom: 23px;
  font-weight: 500;
}
.main-nav__item::before {
  content: "";
  position: absolute;
  right: 0;
  top: -1px;
  left: -70px;
  width: 320px;
  height: 1px;
  background: #e6e6e6;
}
.main-nav__item-link {
  text-decoration: none;
  color: #000000;
}
.main-nav__item-link:hover,
.main-nav__item-link:focus {
  opacity: 0.6;
}
.main-nav__item-link:active {
  opacity: 0.3;
}
.main-nav--closed .main-nav__item {
  display: none;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    width: auto;
    margin-right: 0;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav--closed .main-nav__item {
    display: block;
  }
  .main-nav__item {
    font-size: 18px;
    line-height: 25px;
    text-transform: uppercase;
    font-weight: 400;
    margin-right: 34px;
  }
  .main-nav__item:last-child {
    margin-right: 0;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    left: 0;
    height: 2px;
    background: #68b738;
  }
}
