.catalog {
  width: 100%;
}

.catalog__wrapper {
  padding: 0 20px;
  width: 320px;
  box-sizing: border-box;
  margin: 0 auto;
}

.catalog__title {
  position: relative;
  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
  margin: 15px 0 30px;
  text-align: left;

  &::before {
    content: "";
    position: absolute;
    top: -15px;
    left: -20px;
    width: 320px;
    height: 1px;
    background: @mobile-menu-lines;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: -30px;
    left: -20px;
    width: 320px;
    height: 1px;
    background: @mobile-menu-lines;
    z-index: 3;
  }
}

.catalog__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.catalog__item {
  position: relative;
  box-sizing: border-box;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  padding-bottom: 25px;

  &::after {
    content: "";
    position: absolute;
    bottom: -3px;
    left: -20px;
    width: 320px;
    height: 1px;
    background: @mobile-menu-lines;
    z-index: 3;
  }
}

.catalog__name {
  margin-left: 141px;
  margin-top: 20px;
  margin-bottom: 0;
  width: 80px;
  z-index: 2;
  color: @black;
  text-decoration: none;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  font-weight: 400;
  text-align: left;
}

.catalog__link-img {
  margin-top: -62px;
}

.catalog__name-link {
  margin: 0;
  width: 75px;
  color: @black;
  text-decoration: none;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  font-weight: 400;
}

.catalog__table {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 16px;
  position: relative;
  margin-top: 13px;
  margin-bottom: 0;
}

.catalog__feature {
  text-align: left;
}

.catalog__value {
  margin: 0;
  position: absolute;
  text-align: right;
  left: 101px;

  &--weight {
    width: 40px;
    top: -1px;
  }

  &--type {
    width: 40px;
    top: 14px;
  }

  &--prize {
    width: 40px;
    top: 30px;
  }
}

.catalog__show-more {
  position: relative;
}

.catalog__show-more svg {
  position: absolute;
  top: 27px;
  left: 125px;
}

.catalog__show-more-name {
  margin-top: 80px;
  margin-bottom: 7px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  font-weight: 400;
  text-align: center;
}

.catalog__description {
  font-size: 12px;
  line-height: 16px;
  display: block;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  margin-bottom: 22px;
}

.catalog__order {
  margin: 0 auto;
  width: 100%;
  font-size: 16px;
  line-height: 20px;
}

.catalog__show-all {
  margin: 0 auto 25px;
  background-color: @bg-gray;
  color: @black;
  font-size: 16px;
  line-height: 20px;
}

@media (min-width: @tablet-width) {
  .catalog__title {
    font-size: 60px;
    margin-top: 60px;
    margin-bottom: 170px;
  }

  .catalog__wrapper {
    padding: 0 30px;
    width: 768px;
  }

  .catalog__list {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .catalog__item {
    background-color: @bg-gray;
    width: 324px;
    margin-right: 60px;
    margin-bottom: 140px;
  }

  .catalog__item:nth-child(2n) {
    margin-right: 0;
  }

  .catalog__item:nth-child(n+7) {
    margin-bottom: 20px;
  }

  .catalog__name {
    font-size: 20px;
  }

  .catalog__table {
    font-size: 16px;
  }

  .catalog__order {
    width: 250px;
    margin: 40px;
  }

  .catalog__show-more {
    width: 324px;
  }

  .catalog__show-more-name {
    font-size: 20px;
    margin-bottom: 30px;
  }

  .catalog__description {
    font-size: 16px;
    width: 245px;
  }

  .catalog__show-all {
    margin-top: 270px;
    width: 250px;
  }
}

@media (min-width: @desktop-width)  {
  .catalog__wrapper {
    padding: 0 40px;
    width: 1300px;
  }

  .catalog__item {
    background-color: @bg-gray;
    width: 245px;
    margin-right: 80px;
  }

  .catalog__show-more {
    width: 245px;
  }

  .catalog__item:nth-child(2n) {
    margin-right: 80px;
  }

  .catalog__item:nth-child(4n) {
    margin-right: 0;
  }

  .catalog__item:nth-child(n+5) {
    margin-bottom: 20px;
  }

  .catalog__order {
    width: 165px;
  }

  .catalog__show-all {
    width: 165px;
    margin-top: 250px;
  }
}
